.about-me {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-direction: column;
  flex-wrap: nowrap;
  grid-column: 1 / -1;
  height: 100%;
  max-width: 600px;
  margin: 0 auto; }
  .about-me .card {
    transform: scale(0.9);
    grid-area: card;
    padding: 10px; }
    .about-me .card .h3 {
      font-size: 1.75rem;
      line-height: 1.8375rem;
      font-weight: 300;
      margin: 0 auto; }
    .about-me .card .span {
      display: block;
      margin-top: 20px;
      text-align: center; }
    .about-me .card .scroller {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-direction: row;
      flex-wrap: nowrap; }
    .about-me .card .logo {
      object-fit: cover; }
    .about-me .card .h4 {
      text-align: right;
      margin-top: 20px; }
  .about-me .github,
  .about-me .linkedIn {
    font-size: 1.25rem;
    line-height: 1.3125rem;
    font-weight: 400;
    margin-top: 20px;
    padding: 10px 0;
    text-align: center;
    border-radius: 4.5px;
    border: 2px solid blue;
    letter-spacing: 1px;
    text-transform: uppercase; }
  .about-me .github {
    grid-area: github; }
  .about-me .linkedIn {
    grid-area: linkedIn; }
